.result-container{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 50px;
}

.result-contents{
    width: 600px;
    border:  1px solid rgb(10, 5, 107);
    padding: 50px 80px;
    border-radius: 50px 0px 50px 0px;
    transition: all .5 ease-in-out;
}

.result-contents:hover{
    border:  1px solid rgb(8, 4, 88);
}

h2{
    font-size: 22px;
    line-height: 32px;
    color: white;
    font-style: italic;
}

p{
    font-size: 18px;
    color: rgb(9, 24, 186);
    font-style: italic;
    font-weight: 300;
    margin-top: 22px;
    
}

h3{
    font-size: 52px;
    color: rgb(5, 10, 70);
    margin-bottom: 55px;
    font-weight: 200;
}